<template>
  <div class="container">
    <div class="selector">
      <div class="input-item">
        <label>区域筛选</label>
        <div class="selecteds" v-for="(item,index) in selectedList" :key="index">
          <span>{{item}}</span>
          <div class="closeSelected">
            <img @click="event().toDeleteProvince(index)" style="cursor: pointer;" width="10px" src="@/assets/images/symbols-icons-light-close.png" alt="">
          </div>
        </div>
        <div class="icon" v-if="!showInput">
          <img width="100%" @click="showInput=true" src="@/assets/images/add.png" alt="">
        </div>
        <div class="input" v-if="showInput">
          <input v-model="province" type="text" placeholder="筛选省名称">
          <img @click="event().selectedProvince()" style="margin:0 5px;cursor: pointer;" width="25px" src="@/assets/images/symbols-icons-light-check.png" alt="">
          <img @click="showInput=false" style="cursor: pointer;" width="14px" src="@/assets/images/symbols-icons-light-close.png" alt="">
        </div>
      </div>
    </div>
    <div class="histogram-con">
      <div class="china"></div>
      <div class="who"></div>
      <div id="myChart" class="map"></div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
// var XLSX = require("xlsx");
// 引入基本模板
import { mapState } from "vuex";
import { GRID } from "@/jslib/areaAddress";
var echarts = require("echarts");
// import "echarts/lib/component/title";
// import "echarts/lib/component/legend";
import "echarts/lib/chart/heatmap";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
// import areaData from "@/jslib/areaData";
export default {
  data() {
    return {
      showInput: false,
      province: "",
      selectedList: [], //已筛选的省份列表
      cityList: [],
      seriesData: [],
      dataNum:5,//柱状图显示n条数据
    };
  },
  created() {
    this.getCityList();
    this.handler().getSeriesData();
  },
  mounted() {
    this.drawMap();
  },
  computed: {
    ...mapState(["PM2d5", "CO", "NO2", "PM10", "SO2", "O3", "chartData"])
  },
  methods: {
    getCityList() {
      let allCityList = [];
      this.PM2d5.forEach(item => {
        allCityList.push(item["城市"]);
      });
      this.cityList = this.handler().splitArray(allCityList, this.dataNum);
    },
    drawMap() {
      let myChart = echarts.init(document.getElementById("myChart"));
      myChart.setOption({
        // title: {
        //   text: "世界人口总量",
        //   subtext: "数据来自网络"
        // },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        toolbox: {
          show: true,
          showTitle: true,
          orient: "vertical",
          right: "20px",
          top: "center",
          feature: {
            mark: { show: true },
            dataView: { show: true, readOnly: false },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        // legend: {
        //   data: ["2011年", "2012年", "2013年", "2014年", "2015年"]
        // },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01]
          // data:[0,50,100,150,200,250,300,350,400]
        },
        yAxis: {
          type: "category",
          data: this.cityList
        },
        itemStyle: {
          color: "#32ADFE ",
          barBorderRadius: [0, 10, 10, 0]
        },
        // series: [
        //   {
        //     name: "2011年",
        //     type: "bar",
        //     data: [19325, 23438, 31000, 121594, 134141]
        //   },
        //   {
        //     name: "2012年",
        //     type: "bar",
        //     data: [19325, 23438, 31000, 121594, 134141]
        //   },
        //   {
        //     name: "2013年",
        //     type: "bar",
        //     data: [19325, 23438, 31000, 121594, 134141]
        //   },
        //   {
        //     name: "2014年",
        //     type: "bar",
        //     data: [19325, 23438, 31000, 121594, 134141]
        //   },
        //   {
        //     name: "2015年",
        //     type: "bar",
        //     data: [19325, 23438, 31000, 121594, 134141]
        //   }
        // ]
        series:this.seriesData
      });
    },
    event() {
      return {
        selectedProvince: () => {
          if (this.province === "") {
            alert("筛选条件不能为空！");
          } else if (this.selectedList.indexOf(this.province) !== -1) {
            alert("不能重复筛选");
          } else {
            this.selectedList.push(this.province);
          }
        },
        toDeleteProvince: index => {
          this.selectedList.splice(index, 1);
        }
      };
    },
    network() {
      return {
      };
    },
    handler() {
      return {
        splitArray: (arr, len) => {
          let newArr = [];
          arr.forEach((item, index) => {
            if (index < len) {
              newArr.push(item);
            }
          });
          return newArr;
        },
        cutArray: (arr, len) => {
          let arr_length = arr.length;
          let newArr = [];
          for (let i = 0; i < arr_length; i += len) {
            newArr.push(arr.slice(i, i + len));
          }
          return newArr;
        },
        getSeriesData: () => {
          let values = [];//对象值
          let keys = [];//对象的key
          let newValues = [];//处理原来的对象值，只保留年份污染数据
          let valuesGroup = [];//将污染数据分组
          let haha = [];
          let z = 0;
          this.PM2d5.forEach((item, index) => {
            if (index < this.dataNum) {
              for (let i in item) {
                if (z < 5) {
                  values.push(item[i]);
                  keys.push(i);
                }
                z++;
                if (z === 9) {
                  z = 0;
                  values.forEach(item => {
                    newValues.push(item);
                  });
                  values = [];
                }
              }
            }
          });
          valuesGroup = this.handler().cutArray(newValues, this.dataNum)
          valuesGroup.forEach((a,b) => {
            // for(let i = 0; i<this.dataNum;i++){
            //   haha.push([a[i]])
            // }
            haha.push(a[0])
            // this.seriesData.push({name:keys[index],type:'bar',data:item})//TODO:这里的keys将来可以作为多选的年份，注意需要处理index
          });
          console.log(haha);
          console.log(newValues);
          console.log(valuesGroup);
          // console.log(this.seriesData);
        }
      };
    }
  }
};
</script>

<style scoped lang="scss">
.container {
  position: absolute;
  top: 60px;
  bottom: 0;
  left: 275px;
  right: 0;
  .selector {
    position: absolute;
    z-index: 9999;
    top: 30px;
    left: 30px;
    display: flex;
    align-items: center;
    .input-item {
      display: flex;
      align-items: center;
      .input {
        display: flex;
        align-items: center;
        padding-left: 4px;
        background-color: #32adfe;
        border-radius: 16px;
        width: 280px;
        height: 32px;
        > input {
          padding-left: 15px;
          width: 200px;
          height: 24px;
          border-radius: 16px;
          border: none;
          outline: none;
        }
      }
      label {
        font-size: 16px;
        color: #2c3e50;
        margin-right: 15px;
      }
      .selecteds {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 20px;
        color: #fff;
        width: 86px;
        height: 32px;
        border-radius: 100px;
        background-color: #32adfe;
        .closeSelected {
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          margin-left: 8px;
          background-color: #0f70ca;
          width: 16px;
          height: 16px;
          border-radius: 50%;
        }
      }
      .icon {
        box-sizing: border-box;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 3px;
        border-radius: 50%;
        height: 25px;
        width: 25px;
        font-size: 25px;
        font-weight: lighter;
        background-color: #32adfe;
      }
    }
  }
}
.histogram-con {
  position: relative;
  .china {
    // position:absolute;
    // left: 122px;
    // bottom: 46px;
    // background-color: rgba(149, 226, 254, 0.5);
    // width: 320px;
    // height: 88%;
  }
  .who {
  }
  .map {
    position: fixed;
    margin-top: 35px;
    width: 80%;
    height: 70%;
  }
}
</style>
